* {
  margin: 0;
  padding: 0;
  border: 0px rgb(5, 5, 5);
  /* background-color: rgb(201, 161, 88); */
  
}
.body {
  background-color: rgb(249, 249, 250);
  margin:auto 10%;

}


.header,
.footer {
  background-color: rgb(169, 182, 251);
  color: rgb(61, 52, 52);
  margin: 1rem;
  justify-content: center;
  text-align: center;
  padding: 10px;
  border-radius: 10px;
}
a {
  padding: 0.1rem;
  text-decoration: none;
  color: rgb(138, 70, 28);
  font-size: 16px;
}
a:hover {
  color: black;
  background-color: rgb(167, 176, 176);
  font-size: 12px;
}
.form-heading,
.heading {
  text-align: center;
  font-size: 25px;
}
.aside{
  display: flex;
  margin: auto;
    
}
 

.right{
  flex:10% 1;
  display: block;
  flex-direction: column;
}
.feedback {
  display: block;
  font-size: 18px;
  background-color: rgb(128, 209, 234);
  border-radius: 0px;
  padding: 10px;
  max-width: 800px;
  margin:  40px; 
}

.feedback input {
  text-align: center;
  width: 100%;
  font-size: 23px;
  margin: 10px;
}

.form-group textarea {
  width: 103%;
  height: 20%;
  font-size: 16px;
}
.form-group {
  padding: 0.6rem;
}
.button {
  display: flex;
  margin: 5% 35%;
}

.submit {
  background-color: rgb(44, 55, 63);
  color: rgb(185, 172, 172);
  justify-content: center;
  padding: 10px 20px;
  display: inline-block;
}

.reset {
  padding: 10px;
  justify-content: center;
  color: rgb(216, 199, 199);
  background: #000;
}
 
 @media only screen and (max-width: 200px) {
  .button{
    flex-direction: column;
  }
.feedback{
  margin:  auto 50px;
}
}